html, body {
    width: 100%;
    height: 100%;
}

body {
    background-color: var(--bg-white);
}


/* Главный контейнер страницы */
article {
    width: 100%;
    height: 100%; /* Высота во всю страницу */

    padding: 0px 56px; /* Внешний боковой отступ - 56px */
    overflow-y: auto; /* Скроллбар обычный */
    overflow-x: hidden;
    /* -ms-overflow-style: -ms-autohiding-scrollbar; */

    /* scrollbar-gutter: stable; */
}

/* CSS for other than iOS devices */ 
@supports not (-webkit-touch-callout: none) {
    article {
        overflow-y: overlay; /* Скроллбар поверх */
    }
}

/* Блок с контентом */
section {
    width: 100%;
    height: auto; /* Высота определяется контентом блока */
    position: relative;

    margin: 0 auto;
}

.section-grey {
    background-color: var(--bg-grey);
    border-radius: 40px;
}


.flex-row {
    display: flex;
    flex-direction: row;
}

.flex-col {
    display: flex;
    flex-direction: column;
}