/* Самая первая секция */
.section-main {
    padding: 80px 94px;

    height: 850px;
}

/* Блок навигации */
.section-main nav {
    width: 100%;

    justify-content: space-between;
    align-items: center;
}

/* Обвертка логотипа */
.section-main__logo-wrapper {
    width: 117px;
    height: 38px;
}

/* Контейнер с кнопками */
.section-main nav ul {
    width: 934px;
    justify-content: space-between;
}

/* Кнопки в навигации */
.section-main nav ul li button {
    height: 47px;
    padding: 0 28px;
}

/* Только для десктопных устройств */
@media (hover: hover) {
    .section-main nav ul li button:hover, .section-main nav ul li button:active {
        border: 1px solid var(--border-black);
    }
}


/* Заголовки */
.section-main__titles {
    width: 1140px;
    height: 169px;

    margin-top: 100px;
}

.section-main__titles h1 {
    line-height: 73px;
    letter-spacing: 4.8px;
}

.section-main__titles > h1 {
    text-align: center;
}

.section-main__titles div h1 {
    text-align: end;

    margin-right: 27px;
    margin-top: 23px;
}

.section-main__titles span {
    font-size: inherit;
    font-weight: inherit;

    position: relative;
    z-index: var(--z-main);
}

.section-main__titles span::before {
    content: "";
    width: 313px;
    height: 73px;

    position: absolute;
    top: 0;
    left: -20px;
    z-index: var(--z-under);

    border-radius: 40px;
    background: rgba(55, 187, 255, 0.6);
}