button {
    color: var(--text-black);
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0%;
    text-decoration: none;

    outline: none;
    border: 1px solid #00000000;
    border-radius: 24px;
    background-color: #00000000;

    overflow: hidden;
    user-select: none;
    cursor: pointer;
}



/* Отключена */
button:disabled {
    cursor: not-allowed;
}


/* Только для десктопных устройств */
@media (hover: hover) {
    button:hover, button:active {
        /* background-color: var(--button-hover); */
    }
}